.App {
  font-family: sans-serif;
  text-align: center;
}

.first_div {
  margin: 0px;

  padding: 0px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  border-radius: 0px;
  box-shadow: 1px 2px 3px grey;
  color: rgb(57, 46, 46);
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.App_Bar {
  height: 50px;
  background-color: rgb(30, 23, 23);
  display: flex;
  justify-content: space-between;
}

.App_Bar_title {
  color: white;
  font-size: 25px;
  margin: 10px;
  padding-top: 3px;
  cursor: pointer;
  width: 200px;
  text-shadow: 1px 2px 3px white;
  letter-spacing: 3px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

.App_Bar_Search {
  margin: 10px;
}

.App_Bar_Search > input {
  padding: 5px;
  width: 200px;
}

.App_Bar_Search > button {
  padding: 5px;
  margin-left: 5px;
  width: 80px;
  background-color: white;
  font-size: 15px;
  border-radius: 5px;
  cursor: pointer;
}

.App_Bar_Search > button:hover {
  box-shadow: 1px 2px 2px white;
}

.App_Footer {
  height: 100px;
  background-color: rgb(83, 79, 79);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: none;
  box-shadow: -1px -2px 2px grey;
}

.App_Footer > .part_one {
  color: white;
  cursor: pointer;
  font-size: 25px;
}

.App_Footer > .part_second {
  color: white;
  cursor: pointer;
  font-weight: bold;
}
