.recipe_wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.recipe_itself {
  margin: 10px;
  border: 2px solid rgb(178, 175, 175);
  padding: 10px;
  border-radius: 4px;
  box-shadow: 2px 2px 3px grey;
  width: 430px;
  height: 200px;
  display: flex;
}

.recipe_image {
  justify-content: center;
  margin-right: 10px;
  border-radius: 5px;
  box-shadow: 2px 2px 2px grey;
}

.recipe_label {
  font-weight: bold;
  color: rgb(127, 123, 123);
  margin-right: 10px;
}

.recipe_link {
  font-size: 10px;
  text-decoration: none;
  color: black;
  background-color: white;
  padding: 4px;
  border-radius: 5px;
  margin-top: 4px;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  box-shadow: 2px 2px 2px rgb(102, 97, 97);
  transition: 0.5s;
}

.recipe_link:hover {
  background-color: gainsboro;
  color: rgb(71, 57, 57);
  margin-top: 2px;
}

.recipe_info {
  display: flex;
  flex-direction: column;
  margin: 3px;
  justify-content: space-between;
}

.recipe_calories {
  font-size: 15px;
  margin-top: 5px;
  margin-right: 10px;
  font-weight: bold;
  color: rgb(73, 70, 70);
}
